import React, { useState } from 'react'
import Toolbar from '../toolbar'

/* STYLE DO PRZEBUDOWANIA */
import '../../styles'

const App = () => {

    const [languages, setLanguage] = useState([
        { id: '1', name: 'Apple', count: 5 },
        { id: '2', name: 'Banana', count: 3 }
    ]);


    const [fontSize, setFontSize] =
        useState(100);
    const [contrast, setContrast] =
        useState(false);

    return (
        <React.Fragment>
            <div style={{ fontSize: `${fontSize}%` }}>
                <Toolbar fontSize={fontSize} setFontSize={setFontSize} contrast={contrast} setContrast={setContrast} />
            </div>
        </React.Fragment>
    )
};

export default App